import styles from './ModalCard.module.css';
import Modal from 'react-bootstrap/Modal';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import {useEffect, useState} from "react";
import {Title} from "../ui/Title";
import ImageLoader from "../ui/Loader/Loader";
import {Notify} from "notiflix/build/notiflix-notify-aio";
import heroesAPI from '../../services/servicesApi';
const Status = {
  IDLE: 'idle',
  PENDING: 'pending',
  RESOLVED: 'resolved',
  REJECTED: 'rejected',
};
export const ModalCard = (props) => {
  const [hero, setHero] = useState([]);
  const [status, setStatus] = useState(Status.IDLE);
  const [error, setError] = useState(null);
  const {id} = props;

  // useEffect(() => {
  //   fetchHeroById(id);
  //
  // }, [id]);

  const fetchHeroById=(id)=>{
    if(status==="idle"){
      setStatus(Status.PENDING);
      heroesAPI.fetchHeroById(id)
        .then(results => {
          setHero(results)
          setStatus(Status.RESOLVED);
        })
        .catch(error => {
          setError(error);
          setStatus(Status.REJECTED);
        });


    }


  }

  fetchHeroById(id);
  return (
    <>
      {status === 'pending' ? <ImageLoader/> : null}
      {status === 'rejected' ? Notify.warning(`${error.message}`) : null}
      {status === 'resolved' ? (
        <Modal
          {...props}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              {hero.nickname}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Card>
              <div className={styles.img}>
                <img  variant="top" src={`https://super-heroes-api-ua.herokuapp.com/${hero.imageURL}`} />
              </div>

              <ListGroup className="list-group-flush">
                <ListGroup.Item>Name :
                  <p>{hero.name}</p>
                </ListGroup.Item>
                <ListGroup.Item>Nick name :
                  <p>{hero.nickname}</p>
                </ListGroup.Item>
                <ListGroup.Item>Description :
                  <p>{hero.description}</p>
                </ListGroup.Item>
                <ListGroup.Item>Superpowers :
                  <p>{hero.superpowers}</p>
                </ListGroup.Item>
                <ListGroup.Item>Catch phrase :
                  <p>{hero.catchPhrase}</p>
                </ListGroup.Item>
              </ListGroup>

            </Card>

          </Modal.Body>
          <Modal.Footer>
            <Button onClick={props.onHide}>Close</Button>
          </Modal.Footer>
        </Modal>
      ) : (null)}
    </>


  );
}
